.types-list {
  position: relative;

  display: contents;
  list-style: none;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  &::after {
    z-index: 5;

    @media (min-width: @tablet-width) {
      content: "";

      position: absolute;
      bottom: 0;
      left: 0;

      width: 100%;
      height: 24px;

      .background-image(url("../img/bg-zigzag-fill.svg"), 0 0, repeat);
      background-size: auto 24px;
    }

    @media (min-width: @desktop-width) {
      height: 34px;
      background-size: auto 34px;
    }
  }
}

.types-list__item {
  display: contents;

  background-color: @product-types-basic;

  &--children-toys {
    background-color: @product-types-special;
  }
}

.types-list__link {
  position: relative;

  display: block;
  min-height: 48px;
  padding-top: 24px;
  padding-right: 120px;
  padding-bottom: 31px;
  padding-left: 30px;

  font-weight: bold;
  .line-size(19px, 24px);
  color: @background-basic;

  background-color: @product-types-basic;

  @media (min-width: @tablet-width) {
    min-height: 70px;
    padding-top: 25px;
    padding-right: 150px;
    padding-bottom: 34px;
    padding-left: 37px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 320px;
  }
}

.types-list__link--interior-items {
  &:hover,
  &:focus {
    background-color: @product-types-hover;
  }
}

.types-list__icon-interior {
  position: absolute;
  top: 23px;
  right: 36px;

  display: block;
  width: 40px;
  height: 62px;

  fill: @product-types-icons;
  opacity: 0.5;

  @media (min-width: @tablet-width) {
    top: 30px;
  }
}

.types-list__link--children-toys {
  background-color: @product-types-special;

  &:hover,
  &:focus {
    background-color: @product-types-active;
  }
}

.types-list__icon-toy {
  position: absolute;
  top: 21px;
  right: 27px;

  display: block;
  width: 60px;
  height: 60px;

  fill: @product-types-icons;
  opacity: 0.5;

  @media (min-width: @tablet-width) {
    top: 31px;
    right: 33px;
  }

  @media (min-width: @desktop-width) {
    right: 35px;
  }
}

.types-list__link:hover,
.types-list__link:focus {
  min-height: 53px;

  @media (min-width: @tablet-width) {
    min-height: 75px;
  }
}

.types-list__link:hover .types-list__icon-interior,
.types-list__link:focus .types-list__icon-interior,
.types-list__link:hover .types-list__icon-toy,
.types-list__link:focus .types-list__icon-toy {
  opacity: 0.7;
}

.types-list__link:active .types-list__icon-interior,
.types-list__link:active .types-list__icon-toy {
  opacity: 0.3;
}
